.item {
    width: 180px;
    margin-bottom: 4px;
    padding: 8px;
    border-bottom: 3px solid;
    border-radius: 5px;
    background-color: #fff;
}

.item .image {
    display: block;
    width: 100%;
}

.item .category{
    margin: 15px 9px 10px;
    color: #aaa;
    letter-spacing: 1px;
    font-family: "Trebuchet MS", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
}

.item .category::before{
    content: '';
    display: inline-block;
    width: 19px;
    height: 19px;
    margin-right: 5px;
    border: 2px solid;
    border-radius: 50%;
    vertical-align: -5px;
}

.item .description {
    margin: 10px;
    line-height: 1.5;
}

.item > a {
    display: block;
    margin: -8px -8px -11px;
    padding: 8px 8px 11px;
    border-radius: inherit;
    color: #777;
    transition: all 0.3s;
}

.item > a:hover {
    box-shadow: 0 0 6px -1px rgba(0, 0, 0, 0.3);
    opacity: 0.8;
}

.item-appetizer,
.item-appetizer .category::before {
    border-color: #FFC0CB;
}

.item-maindish,
.item-maindish .category::before {
    border-color: #76C047;
}

.item-column,
.item-column .category::before {
    border-color: #FFF100;
}

.item-breaktime,
.item-breaktime .category::before {
    border-color: #C1EFFF;
}
